.EndTitle{
    color: #f8b206;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    font-size: 4vw;
    margin-bottom: 2vh;
}

.EndGif{
    margin: auto;
    display: flex;
    width: 50vh;
    margin-bottom: 10px;
}

.EndH2{ 
    display: flex;
    font-size: 2vw;
    justify-content: center;
    color: white;
    margin-bottom: 1vh;
}

.EndText{
    display: flex;
    justify-content: center;
    color: white;
}

.EndButton{
    display: flex;
    margin: auto;
    margin-top: 15px;
    background-color: #ba2184;
    border-radius: 5px;
    width: 30%;
    color: white;
    font-size: 1.3em;
    justify-content: center;
}

.EndButton:hover{
    cursor: pointer;
}